import React from 'react'
import PropTypes from 'prop-types'

import Image from './Image'
import './Card.css'

const Card = ({
  title,
  backgroundImage,
  large,
  className = ''
}) => {
  if (large) className += ' Card-large'
  return (
    <div className="Card">
        <img src={backgroundImage} alt="Snow" style="width:100%;" />

    <h1 className="CardText">{title}</h1>
    </div>
  )
}

Card.propTypes = {
  title: PropTypes.string
}

export default Card
